import React from "react";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";

export default function Conclusions() {
  return (
    <div>
      <Card style={{ marginTop: "1rem", paddingTop: "1rem" }}>
        <CardHeader color="primary">Conclusions</CardHeader>
        <CardBody>
          <p>
            I think it's important to consider both the most common
            restaurants in each area and the most popular restaurants in
            each area. A potential restaurant opener would want to avoid
            opening in clusters where their restaurant is too common. They would
            also want to open in a location where their restaurant type is
            more popular. There is much more work that can be done to analyze this
            data further such as looking into which specific restaurants
            are popular or common in each neighborhood.
          </p>
        </CardBody>
      </Card>
      <Card style={{ marginTop: "1rem", paddingTop: "1rem" }}>
          <CardHeader color="primary">References</CardHeader>
          <CardBody>
          <ol>
            <li><a href="https://opendata.arcgis.com/datasets/7c5e82ef83834de2ad2478efc86744ae_0.geojson"
                   target="_blank" rel="noopener noreferrer">https://opendata.arcgis.com/datasets/7c5e82ef83834de2ad2478efc86744ae_0.geojson</a>
            </li>
            <li><a href="https://www.zillow.com/baton-rouge-la/home-values/"
                   target="_blank" rel="noopener noreferrer">https://www.zillow.com/baton-rouge-la/home-values/</a>
            </li>
            <li><a href="https://foursquare.com/" target="_blank" rel="noopener noreferrer">https://foursquare.com/</a></li>
            <li><a href="https://github.com/travistheall/Coursera_Capstone" target="_blank" rel="noopener noreferrer">My Github Repo</a></li>
          </ol>
          </CardBody>
      </Card>
    </div>
  );
}
