import React, {Component} from "react";
import Pagination from "react-js-pagination";
import Rows from "./Rows";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
let page = 1
let API = 'http://127.0.0.1:8000/api/maindesc/?page=' + page;


export default class Nutrition extends Component {
  constructor(props) {
    super(props);
    this.state = {
      url: API,
      code: '',
      desc: '',
      page: 1,
      format: "json",
      results: [],
      activePage: 1,
      searchableURL: '',
      pageURL: '',
      searchTerm: '',
    };
  };

  componentDidMount() {
    console.log('2 componentDIDMount')
    this.setState({results: this.state.results})
    fetch(this.state.url)
      .then(response => response.json())
      .then(data => this.setState({results: data.results, count: data.count}));
  };

  updatePageURL(page) {
    const pStr = 'page='
    let tempURL = this.state.url
    let tempURL1 = tempURL.slice(0, (tempURL.search(pStr) + pStr.length))
    let newURL = tempURL1 + String(page)
    return newURL
  };

  handlePageChange(pageNumber) {
    this.setState({activePage: pageNumber});
    fetch(this.updatePageURL(pageNumber))
      .then(response => response.json())
      .then(data => this.setState({results: data.results}));
  };

  searchList(e) {
    let currentVal = e.target.value;
    let newURL = ''
    if (isNaN(parseInt(currentVal))) {
      newURL = `http://127.0.0.1:8000/api/maindesc/?mainfooddescription=${currentVal}&page=1`;
    } else {
      newURL = `http://127.0.0.1:8000/api/maindesc/?foodcode=${currentVal}&page=1`;
    }
    this.setState({
        searchableURL: newURL,
        activePage: 1
      }
    );
    return newURL
  }

  handleSearch(e) {
    e.preventDefault();
    this.setState({
      url: this.state.searchableURL,
      activePage: 1
    })
    fetch(this.state.searchableURL)
      .then(response => response.json())
      .then(data => this.setState({results: data.results, count: data.count}));
  };

  render() {
    let {activePage} = this.state;
    let {count} = this.state;
    return (
      <div style={{
        "background": "#FFFFFF",
        "position": "relative",
        "zIndex": "3",
        "margin": "-60px 30px 0px",
        "borderRadius": "6px",
        "boxShadow": "0 16px 24px 2px rgba(0, 0, 0, 0.14), 0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2)"
      }}>
        <Card>
          <CardHeader color="success">Main Food Search</CardHeader>
          <div>
            <form>
              <h4><label htmlFor='search'>Search: </label></h4>
              <input id='search' type="text" placeholder="Search" name='text' onChange={this.searchList.bind(this)}/>
              <button className="btn btn-dark" onClick={this.handleSearch.bind(this)}>Search</button>
            </form>
          </div>
          <table className="table table-hover table-dark">
            <thead>
            <tr>
              <th scope="col">Food Code</th>
              <th scope="col">Main Description</th>
            </tr>
            </thead>
            <tbody>
            {this.state.results.map((f, i) => <Rows food={f} tableIndex={i} key={'row-' + i}/>)}
            </tbody>
          </table>
          <div>
            <nav aria-label="Page navigation" colSpan={"2"}>
              <Pagination
                activePage={activePage}
                itemsCountPerPage={10}
                itemClass={"page-item"}
                linkClass={"page-link"}
                totalItemsCount={count}
                pageRangeDisplayed={5}
                onChange={this.handlePageChange.bind(this)}
              />
            </nav>
          </div>
        </Card>
      </div>
    );
  }
}
