import React, { useState, useEffect } from 'react';
import classNames from "classnames";
import {  Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, makeStyles, Input }  from '@material-ui/core';
import Pagination from '@material-ui/lab/Pagination';
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import Button from "../../components/CustomButtons/Button.js";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import LandingStyles from "assets/jss/material-kit-react/views/landingPage.js";
import Row from './components/Row.js'
import { useFetch } from './hooks/useFetch.js'
import { useForm } from './hooks/useForm.js'

const useLandingStyles = makeStyles(LandingStyles);

const createRows = (foodcode, mainfooddescription, calories, fat, carbs, protein, price, index) => {
  return (
    <Row key={index} row={{
      foodcode,
      mainfooddescription,
      calories,
      fat,
      carbs,
      protein,
      price,
      history: [
        {date: '2020-01-05', customerId: '11091700', amount: 3},
        {date: '2020-01-02', customerId: 'Anonymous', amount: 1}
      ]
    }}/>)
};

export default function MaterialTableDemo() {
  const [page, setPage] = useState(1);
  const [url, setUrl] = useState(`http://127.0.0.1:8000/api/maindesc/?mainfooddescription=burger&page=${page}`);
  const {data, loading} = useFetch(url);
  const [rows, setRows] = useState(data.results.map((result, index) => createRows(result.foodcode, result.mainfooddescription, 159, 6.0, 24, 4.0, 3.99, index)));
  const [values, handleChange] = useForm({searchTerm: ''});

  useEffect(() => {
    console.log('use effect url', url)
    setRows(data.results.map((result, index) => createRows(result.foodcode, result.mainfooddescription, 159, 6.0, 24, 4.0, 3.99, index)))
  }, [data])

  const handlePageClick = (e, pageNumber) => {
    setUrl(url.replace(`page=${page}`, `page=${pageNumber}`))
    setPage(pageNumber)
    console.log('state page', page)
    console.log('function pagenumber', pageNumber)
    console.log('url', url)
  }

  const handleSearch = (e) => {
    e.preventDefault()
    let descURL = `http://127.0.0.1:8000/api/maindesc/?mainfooddescription=${values.searchTerm}&page=1`
    let codeURL = `http://127.0.0.1:8000/api/maindesc/?foodcode=${values.searchTerm}&page=1`
    let newURL = isNaN(parseInt(values.searchTerm)) ? descURL : codeURL
    setUrl(newURL)
    setPage(1)
    console.log('state page', page)
    console.log('url', url)
  }

  const landingClasses = useLandingStyles();
  return (
      <div className={classNames(landingClasses.main, landingClasses.mainRaised)}>
        <div className={landingClasses.container}>
          <Card>
            <CardHeader color="success">Main Food Search</CardHeader>
              <GridContainer>
                <GridItem xs={12} sm={12} md={4}>
                    <Input
                      name="searchTerm"
                      id="maindDescSearch"
                      inputProps={{placeholder: "Search"}}
                      formControlProps={{fullWidth: true}}
                      type={'text'}
                      value={values.searchTerm}
                      onChange={handleChange}
                    />
                  <Button onClick={handleSearch}>Search</Button>
              </GridItem>
            </GridContainer>
            <TableContainer component={Paper}>
              <Table aria-label="collapsible table">
                <TableHead>
                  <TableRow>
                    <TableCell />
                    <TableCell>Food Code</TableCell>
                    <TableCell align="left">Main Description</TableCell>
                    <TableCell align="right">Calories</TableCell>
                    <TableCell align="right">Fat&nbsp;(g)</TableCell>
                    <TableCell align="right">Carbs&nbsp;(g)</TableCell>
                    <TableCell align="right">Protein&nbsp;(g)</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {rows}
                </TableBody>
              </Table>
              <Pagination count={data.count} page={Number(page)} onChange={handlePageClick} />
            </TableContainer>
          </Card>
      </div>
    </div>
  );
}

