import React from "react";
// material-ui components
// core components
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";

export default function TargetAud() {
  return (
    <Card style={{ marginTop: "1rem", paddingTop: "1rem" }}>
      <CardHeader color="primary">Target Audience</CardHeader>
      <CardBody>
        <p>
          Restaurants are a great investment, if planned properly.
          Unfortunately, almost half of all restaurants fail during the first
          year of opening. My target audience would be anyone who is looking to
          start in the restaurant business within Baton Rouge area. There are
          many factors to consider such as location, service style, and the
          actual food being sold.
        </p>
        <p>
          It is essential to understand the variety of restaurants in each
          neighborhood. Identifying a market gap within a specific neighborhood
          could lead to that restaurant being successful. Versus opening a
          restaurant in a neighborhood that doesn{""}t need your food and
          failing.
        </p>
      </CardBody>
    </Card>
  );
}
