import React from "react";
// material-ui components
import { makeStyles } from "@material-ui/core/styles";
import classNames from "classnames";
// core components
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "../../../components/Card/CardHeader";
import GridContainer from "components/Grid/GridContainer.js";

import Map1 from "assets/data/folium map.png";
import ElbowD from "assets/data/elbowDistortion.png";
import ElbowI from "assets/data/elbowintertia.png";
import MapClus1 from "assets/data/folium Cluster.png";
import MapClus2 from "assets/data/folium Pop.png";
import imagesStyles from "assets/jss/material-kit-react/imagesStyles.js";
import TabbedCard1 from "./TabbedCard1";
import TabbedCard2 from "./TabbedCard2";

import { cardTitle } from "assets/jss/material-kit-react.js";

import Button from "components/CustomButtons/Button.js";

const styles = {
  ...imagesStyles,
  cardTitle,
};

const useStyles = makeStyles(styles);

export default function Results() {
  const classes = useStyles();
  const imageClasses = classNames(classes.imgRaised);
  return (
    <div>
      <div
        style={{
          display: "flex",
          "justify-content": "space-around",
        }}
      >
        <Card style={{ marginTop: "1rem", paddingTop: "1rem" }}>
          <CardHeader color="primary">Results</CardHeader>
          <CardBody>
            <h4 className={classes.cardTitle}>
              Baton Rouge Neighborhoods Most Common Restaurants Clusters
            </h4>
            <p>
              A map created from K-means clustering of the East Baton Rouge
              Area. Zillow Housing Score was added to make a Choropleth map.
              Data could not be obtained for the black neighborhoods. The darker
              green neighborhoods where housing is more expensive. While the
              lighter neighborhoods are cheaper.
            </p>
            <Button
              type="button"
              color="info"
              href="https://www.zillow.com/baton-rouge-la/home-values/"
              className="btn btn-primary btn-sm text-left"
              target="_blank"
              rel="noreferrer"
            >
              Housing Data
            </Button>
            <Button
              type="button"
              color="info"
              href="https://github.com/travistheall/Coursera_Capstone/blob/master/notebooks/Api%20Popular%20Restaurants.ipynb"
              rel="noreferrer"
              target="_blank"
            >
              Jupytr Notebook
            </Button>
          </CardBody>
          <img
            className={classes.imgCardBottom}
            src={Map1}
            alt="Card-img-cap"
          />
          <CardBody>
            <p>
              I utilized the Foursquare API to explore the restaurants in each
              neighborhood and segment them. I designed the limit as 50 venues.
              I used the section {'"'}food{'"'} and sorted by popularity. The
              radius is the maximum distance between the two geolocations
              converted to meters from each neighborhood based on their given
              latitude and longitude information. The radius is the maximum
              distance between the two geolocations converted to meters from
              each neighborhood based on their given latitude and longitude
              information.
            </p>
            <p>
              The search resulted in 1514 venues with 57 unique Restaurant
              Categories
            </p>
            <p>
              Each of these results was run through a KMeans clustering
              algorithm. The best K was determined using the elbow method. 3
              clusters was determined to be the best grouping.
            </p>
            <GridContainer
              style={{
                "flex-direction": "row",
                "justify-content": "space-around",
              }}
            >
              <img
                src={ElbowD}
                alt={"Elbow Method using Distortion"}
                className={imageClasses}
              />
              <img
                src={ElbowI}
                alt={"Elbow Method using Inertia"}
                className={imageClasses}
              />
            </GridContainer>
          </CardBody>
        </Card>
      </div>
      <div>
        <Card style={{ marginTop: "1rem", paddingTop: "1rem" }}>
          <CardHeader color="primary">
            Baton Rouge Neighborhoods Most Common Restaurants Clusters
          </CardHeader>
          <CardBody>
            <p>
              A map created from K-means clustering of the East Baton Rouge
              Area. The Zillow Housing Score was added to make a Cholorpleth
              map. Data could not be obtained for the black neighborhoods. The
              darker green neighborhoods where housing is more expensive. While
              the lighter green neighborhoods are cheaper.
            </p>
            <Button
              color="info"
              href="https://www.zillow.com/baton-rouge-la/home-values/"
              className="btn btn-primary btn-sm text-left"
              rel="noreferrer"
              target="_blank"
            >
              Housing Data
            </Button>
            <Button
              color="info"
              href="https://github.com/travistheall/Coursera_Capstone/blob/master/notebooks/Api%20Popular%20Restaurants.ipynb"
              className="btn btn-primary btn-sm text-left"
              rel="noreferrer"
              target="_blank"
            >
              Jupytr Notebook
            </Button>
            <img
              className={classes.imgCardBottom}
              src={MapClus1}
              alt="Card-img-cap"
            />
          </CardBody>
        </Card>
        <TabbedCard1 />
        <div>
          <Card style={{ marginTop: "1rem", paddingTop: "1rem" }}>
            <CardHeader color="primary">
              Baton Rouge Neighborhoods Popular Restaurants Clusters
            </CardHeader>
            <CardBody>
              <p>
                A map created from K-means clustering of the East Baton Rouge
                Area. The Zillow Housing Score was added to make a Choropleth
                map. Data could not be obtained for the black neighborhoods. The
                darker green neighborhoods where housing is more expensive.
                While the lighter green neighborhoods are cheaper.
              </p>
              <Button
                color="info"
                href="https://www.zillow.com/baton-rouge-la/home-values/"
                className="btn btn-primary btn-sm text-left"
                rel="noreferrer"
                target="_blank"
              >
                Housing Data
              </Button>
              <Button
                color="info"
                href="https://github.com/travistheall/Coursera_Capstone/blob/master/notebooks/Api%20Popular%20Restaurants.ipynb"
                className="btn btn-primary btn-sm text-left"
                rel="noreferrer"
                target="_blank"
              >
                Jupytr Notebook
              </Button>
              <img
                className={classes.imgCardBottom}
                src={MapClus2}
                alt="Card-img-cap"
              />
            </CardBody>
          </Card>
          <TabbedCard2 />
        </div>
      </div>
    </div>
  );
}
