import React, {Component} from "react";

class Rows extends Component {

  render() {
    let {tableIndex} = this.props;
    let {food} = this.props;
    return (<tr id={"table-rows-" + tableIndex}>
      <th scope="row" id={"foodcode" + tableIndex}>{food['foodcode']}</th>
      <td id={"description-" + tableIndex}>{food['mainfooddescription']}</td>
    </tr>)
  }
}

export default Rows;