import React from "react";
// material-ui components
import { makeStyles } from "@material-ui/core/styles";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
// core components
import CustomTabs from "components/CustomTabs/CustomTabs.js";
// @material-ui/icons
import { LooksTwo, ExposureZero, LooksOne } from "@material-ui/icons";

// images
import image1 from "assets/data/MostCommonCluster0.png";
import image2 from "assets/data/MostCommonCluster1.png";
import image3 from "assets/data/MostCommonCluster2.png";

const styles = {
  textCenter: {
    textAlign: "center",
  },
};

const useStyles = makeStyles(styles);

export default function TabbedCard1() {
  const classes = useStyles();
  return (
    <div style={{ marginTop: "1rem", paddingTop: "1rem" }}>
      <CustomTabs
        headerColor="primary"
        tabs={[
          {
            tabName: "Cluster 0",
            tabIcon: ExposureZero,
            tabContent: (
              <Card>
                <CardHeader color="info">
                  Cluster 0 (Red Dots) Common Categories
                </CardHeader>
                <img
                  src={image1}
                  alt="Card-img-cap"
                  style={{ "max-width": "100%", "align-self": "center" }}
                />
                <CardBody>
                  <p className={classes.textCenter}>
                    Most Common Restaurants in this cluster are primarily
                    American Restaurants and burger joints. These are located in
                    the Northern Most neighborhoods and the Southern half of
                    Rouge. This is located in the area where the average housing
                    index is the highest at $218,431
                  </p>
                </CardBody>
              </Card>
            ),
          },
          {
            tabName: "Cluster 1",
            tabIcon: LooksOne,
            tabContent: (
              <Card>
                <CardHeader color="info">
                  Cluster 1 (South Purple Dots) Common Categories
                </CardHeader>
                <img
                  src={image2}
                  alt="Card-img-cap"
                  style={{ "max-width": "100%", "align-self": "center" }}
                />
                <CardBody>
                  <p className={classes.textCenter}>
                    Most Common Restaurants in this cluster are {'"'}Food{'"'}{" "}
                    this means that they are not typical restaurants. These are
                    places that serve food, but aren{"'"}t restaurants like a
                    food bank. This is located in the area where the average
                    housing index is the lowest at $77,200
                  </p>
                </CardBody>
              </Card>
            ),
          },
          {
            tabName: "Cluster 2",
            tabIcon: LooksTwo,
            tabContent: (
              <Card>
                <CardHeader color="info">
                  Cluster 2 (Green Dots) Common Categories
                </CardHeader>
                <img
                  src={image3}
                  alt="Card-img-cap"
                  style={{ "max-width": "100%", "align-self": "center" }}
                />
                <CardBody>
                  <p className={classes.textCenter}>
                    Most Common Restaurants in this cluster are Fast Food Take
                    Out locations. This is located in the area where the average
                    housing index is in the middle at $92,246
                  </p>
                </CardBody>
              </Card>
            ),
          },
        ]}
      />
    </div>
  );
}
