import React from "react";
// material-ui components
import { makeStyles } from "@material-ui/core/styles";
// core components
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";

import { cardTitle } from "assets/jss/material-kit-react.js";

const styles = {
  cardTitle,
};

const useStyles = makeStyles(styles);

export default function Intro() {
  const classes = useStyles();
  return (
    <Card style={{ marginTop: "5rem", paddingTop: "3rem" }}>
      <CardHeader color="primary">Intro</CardHeader>
      <CardBody>
        <h4 className={classes.cardTitle}>General Description</h4>
        <p>
          Baton Rouge is the capital city of Louisiana. There are 220,236 people
          living in Baton Rouge alone, making it the second largest city in
          Louisiana behind New Orleans. There are over 800,000 people living in
          the Baton Rouge metropolitan area, and the population has grown 3.4%
          within the past 10 years. I will be focusing on East Baton Rouge
          Parish, the most populated parish in the Baton Rouge Metropolitan
          area. **
        </p>
        <p>
          It is home to Louisiana State University (LSU), one of the top-rated
          universities in Louisiana. LSU is cheap for both in and out of state
          students combined with having a high acceptance rate means there are
          always new students moving into Baton Rouge. Freshman are required to
          live on campus their first year. It is also the 9th largest port in
          the United States. Workers come and go from the Mississippi River with
          goods to ship and hungry bellies every day.{" "}
        </p>
        <p>
          All these residents and workers need a place to eat. Students get
          tired of dining halls and don{"'"}t have kitchens in their dorms.
          Shipping workers are excited to have a real meal after coming from
          overseas. Regular working-class adults sometimes forget or are too
          lazy to cook for themselves. Louisiana has a diverse food culture and
          almost all styles of food have a place in Baton Rouge.{" "}
        </p>
        <p className={"text-muted"}>
          **For those unfamiliar with Louisiana, a parish is akin to a county in
          other states.
        </p>
      </CardBody>
    </Card>
  );
}
